@import "settings"
@import "_mixins/_mixins"
@import "ui"

/* main page */
.side-banner
  min-height: calc(100vh - 80px)
  display: flex
  flex-direction: column
  align-items: flex-start
  justify-items: flex-start
  overflow: hidden
  @media (max-width: 960px)
    min-height: auto
    height: auto
  .container
    flex: 1 0 100%
  &__wrapper
    padding: 0 3.15% 0 16.25%
    width: 100%
    @media (max-width: 1200px)
      padding: 0 3.15% 0 8.25%
    @media (max-width: 1024px)
      padding: 0 0 0 25px
    @media (max-width: 960px)
      padding: 30px
    @media (max-width: 576px)
      padding: 30px 0
  &__content
    display: flex
    align-items: stretch
    justify-content: flex-start
    flex-wrap: wrap
    height: 100%
    min-height: calc(100vh - 80px)
    @media (max-width: 960px)
      min-height: auto
    &__left-side
      position: relative
      flex: 0 0 50%
      max-width: 50%
      @media (max-width: 960px)
        flex: 0 0 calc(100% + 40px)
        max-width: calc(100% + 40px)
        margin: 0 -20px
      .side-image
        display: block
        width: 50vw
        height: auto
        position: absolute
        top: 0
        right: 0
        @media (max-width: 1440px)
          width: auto
          height: 100%
          min-width: 50vw
        @media (max-width: 960px)
          width: 100%
          height: auto
          min-width: auto
          position: relative
    &__right-side
      flex: 0 0 50%
      max-width: 50%
      display: flex
      align-items: center
      justify-content: flex-start
      @media (max-width: 960px)
        flex: 0 0 100%
        max-width: 100%
      .soon
        display: inline-block
        text-transform: uppercase
        color: $gray-medium
        font-size: 20px
        line-height: 20px
        letter-spacing: 1px
        margin-bottom: 16px
        @media (max-width: 414px)
          font-size: 18px
          line-height: 18px
      .price
        display: inline-block
        text-transform: uppercase
        color: $bronze-gold
        font-size: 30px
        line-height: 30px
        letter-spacing: 2px
        margin-top: 19px
        @media (max-width: 414px)
          font-size: 25px
          line-height: 25px
      .properties-list
        margin-top: 27px
        &__item
          display: flex
          align-items: center
          justify-content: flex-start
          padding: 20px 0
          border-bottom: 1px solid rgba($dark-black, .1)
          @media (max-width: 1200px)
            padding: 15px 0
          @media (max-width: 414px)
            padding: 10px 0
          .icon-block
            flex: 0 0 20px
            width: 20px
            height: 20px
            margin-right: 20px
            .icon
              display: block
              width: auto
              max-width: 100%
          .descr-block
            display: flex
            align-items: center
            justify-content: space-between
            font-size: 18px
            line-height: 18px
            color: $gray-medium
            flex: 0 0 calc(100% - 40px)
            max-width: calc(100% - 40px)
            width: 100%
            @media (max-width: 1200px)
              font-size: 16px
              line-height: 16px
            @media (max-width: 414px)
              font-size: 14px
              line-height: 14px
            span:nth-of-type(2)
              color: $dark-black
              font-weight: 600
      .btns-row
        display: flex
        align-items: center
        justify-content: flex-start
        flex-wrap: wrap
        margin-top: 49px
        @media (max-width: 960px)
          justify-content: center
        .btn-style + .btn-style
          margin-left: 32px
          @media (max-width: 1024px)
            margin-left: 15px
          @media (max-width: 960px)
            margin-left: 30px
          @media (max-width: 414px)
            margin: 20px 0 0 0
        .btn-style
          @media (max-width: 414px)
            display: flex
            width: 100%
            justify-content: center
  &.reverse-style
    .side-banner__content
      flex-direction: row-reverse
      justify-content: flex-end
      &__left-side
        .side-image
          right: auto
          left: 0
    .side-banner__wrapper
      padding: 0 16.25% 0 3.15%
      @media (max-width: 1200px)
        padding: 0 8.25% 0 3.15%
      @media (max-width: 1024px)
        padding: 0 25px 0 0
      @media (max-width: 960px)
        padding: 30px
      @media (max-width: 576px)
        padding: 30px 0
  &.light-style
    background-color: $light-gray
    .side-banner__title
      color: $dark-gray
  &.gray-style
    background-color: $dark-gray
    .side-banner__title
      color: #fff
    .side-banner__content__right-side .properties-list__item
      border-bottom: 1px solid rgba(#fff, .1)
      .descr-block
        color: $gray-light
        span:nth-of-type(2)
          color: #fff
  &.black-style
    background-color: $dark-black
    .side-banner__title
      color: #fff
    .side-banner__content__right-side .properties-list__item
      border-bottom: 1px solid rgba(#fff, .1)
      .descr-block
        color: $gray-light
        span:nth-of-type(2)
          color: #fff

.purchase-section
  background-repeat: no-repeat
  background-position: center
  background-size: cover
  color: #fff
  min-height: calc(100vh - 80px)
  position: relative
  display: flex
  flex-direction: column
  align-items: flex-start
  justify-items: flex-start
  &:after
    content: ''
    display: block
    width: 100%
    height: 100%
    position: absolute
    top: 0
    left: 0
    background: #181B1D
    opacity: 0.8
  .container
    flex: 1 0 100%
    position: relative
    z-index: 1
    display: flex
    flex-direction: column
    align-items: center
    justify-content: center
  &__wrapper
    padding: 5% 0
    @media (max-width: 414px)
      padding: 40px 0
  &__title
    text-align: center
  &__content
    display: flex
    flex-wrap: wrap
    align-items: stretch
    justify-content: center
    margin-top: 9.5%
    @media (max-width: 1400px)
      margin-top: 5.5%
    @media (max-width: 414px)
      margin-top: 40px
    &__item
      flex: 0 0 calc(33.3% - 60px)
      max-width: calc(33.3% - 60px)
      margin: 0 0 30px
      @media (max-width: 1024px)
        flex: 0 0 100%
        max-width: 100%
        display: flex
        flex-direction: row
        align-items: flex-start
        justify-content: flex-start
        margin: 0 0 60px 0 !important
      @media (max-width: 768px)
        display: block
      @media (max-width: 576px)
      &:nth-of-type(2n)
        margin: 0 auto 30px
      .icon-block
        display: flex
        align-items: center
        justify-content: center
        width: 100px
        height: 100px
        background-color: $bronze-gold
        border-radius: 50%
        margin: 0 auto
        @media (max-width: 1024px)
          flex: 0 0 100px
          max-width: 100px
        @media (max-width: 360px)
          flex: 0 0 60px
          max-width: 60px
          height: 60px
          .icon
            max-width: 30px
      .descr-block
        text-align: center
        @media (max-width: 1024px)
          text-align: left
          flex: 1 1 calc(100% - 100px)
          max-width: calc(100% - 100px)
          padding-left: 30px
        @media (max-width: 768px)
          text-align: center
          padding: 0
          max-width: 100%
          margin-top: 20px
        .item-title
          display: inline-block
          font-weight: 300
          font-size: 40px
          line-height: 40px
          text-transform: uppercase
          margin-top: 40px
          @media (max-width: 1200px)
            font-size: 30px
            line-height: 30px
            margin-top: 30px
          @media (max-width: 1024px)
            margin-top: 0
          @media (max-width: 360px)
            font-size: 25px
            line-height: 25px
        .item-descr
          font-size: 18px
          line-height: 27px
          margin-top: 20px
          @media (max-width: 1600px)
            font-size: 16px
            line-height: 25px
            padding: 0 10px
          @media (max-width: 1024px)
            padding: 0
          @media (max-width: 768px)
            max-width: 600px
            margin: 20px auto 0
          @media (max-width: 360px)
            font-size: 14px
            line-height: 22px
        .item-link
          margin-top: 38px
          @media (max-width: 1024px)
            margin-top: 20px

  .scroll-btn
    position: absolute
    bottom: 0
    left: 0
    right: 0
    margin: 0 auto
    z-index: 1
.cars-section
  background-color: #F2F2F2
  min-height: calc(100vh - 80px)
  display: flex
  flex-direction: column
  align-items: center
  justify-content: center
  &__wrapper
    padding: 5% 0
    @media (max-width: 414px)
      padding: 40px 0 0
  &__title
    text-align: center
  &__content
    display: flex
    flex-wrap: wrap
    align-items: stretch
    justify-content: flex-start
    margin-top: 95px
    @media (max-width: 1400px)
      margin-top: 55px
    &__item
      flex: 0 0 calc(33.3% - 27px)
      max-width: calc(33.3% - 27px)
      margin: 0 40px 40px 0
      @media (max-width: 1200px)
        flex: 0 0 calc(33.3% - 15px)
        max-width: calc(33.3% - 15px)
        margin: 0 20px 20px 0
      @media (max-width: 768px)
        flex: 0 0 calc(50% - 15px)
        max-width: calc(50% - 15px)
        margin: 0 30px 30px 0
        &:nth-of-type(3n)
          margin-right: 30px
        &:nth-of-type(2n)
          margin-right: 0
      @media (max-width: 576px)
        flex: 0 0 100%
        max-width: 100%
        width: 100%
      &:nth-of-type(3n)
        margin-right: 0
      .image-block
        overflow: hidden
        &:hover
          .item-image
            transform: scale(1.1)
        .item-link
          display: block
        .item-image
          display: block
          width: auto
          max-width: 100%
          transition: transform .5s
      .descr-block
        background-color: #fff
        padding: 30px 30px 35px
        @media (max-width: 1200px)
          padding: 20px 20px 30px
        .item-title
          text-transform: uppercase
          font-size: 40px
          line-height: 40px
          font-weight: 400
          text-transform: uppercase
          @media (max-width: 1024px)
            font-size: 32px
            line-height: 32px
        .item-price
          display: inline-block
          margin-top: 10px
          color: $gray-medium
          font-size: 18px
          line-height: 18px
        ul
          margin: 16px 0 0
          padding: 0
          list-style-type: none
          font-size: 14px
          line-height: 21px
          li
            display: block
            width: 100%
            position: relative
            padding: 8px 0 8px 0
            border-bottom: 1px solid #DEDEDE
            margin-top: 4px
            display: inline-flex
            align-items: center
            justify-content: flex-start
            &:before
              content: '\e800'
              font-family: Fontello
              font-style: normal
              font-weight: normal
              speak: never
              display: inline-block
              margin: 0 6px 0 -3px
              text-decoration: inherit
              text-align: center
              font-variant: normal
              text-transform: none
              color: $bronze-gold
              font-size: 17px
              line-height: 20px
              position: relative
              top: -2px
        .item-link
          margin-top: 30px
          @media (max-width: 1024px)
            display: flex
            align-items: center
            justify-content: center
            width: 100%
.map-section
  position: relative
  padding: 5.5% 0
  min-height: 630px
  @media (max-width: 768px)
    padding: 0
  .container
    position: relative
    z-index: 1
    width: auto
  &__wrapper
    position: absolute
    top: 0
    left: 0
    width: 100%
    height: 100%
    background-color: #aaa // tmp
    @media (max-width: 768px)
      position: relative
      min-height: 500px
    iframe
      display: block
      width: 100%
      height: 100%
      @media (max-width: 768px)
        position: absolute
        top: 0
        left: 0
  &__content
    background-color: #fff
    box-shadow: 0px 0px 10px rgba(0, 49, 69, 0.1)
    max-width: 540px
    padding: 32px 30px 30px
    @media (max-width: 960px)
      max-width: 500px
    @media (max-width: 768px)
      box-shadow: none
      max-width: 100%
      padding: 30px 20px
    @media (max-width: 360px)
      padding: 30px 0
    .map-title
      font-weight: 400
      font-size: 30px
      line-height: 30px
      text-transform: uppercase
      @media (max-width: 360px)
        font-size: 28px
        line-height: 28px
    .map-subtitle
      display: inline-block
      color: $gray-medium
      font-weight: 400
      font-size: 18px
      line-height: 18px
      margin-top: 10px
      @media (max-width: 360px)
        font-size: 15px
        line-height: 15px
    address
      display: block
      padding-left: 40px
      margin-top: 25px
      font-size: 14px
      line-height: 20px
      font-style: normal
      position: relative
      &:before
        content: '\e805'
        font-family: Fontello
        font-style: normal
        font-weight: normal
        speak: never
        display: block
        margin: 0
        text-decoration: inherit
        text-align: center
        font-variant: normal
        text-transform: none
        font-size: 20px
        line-height: 20px
        position: absolute
        top: 0
        left: 2px
        color: $bronze-gold
    .phone-link
      display: inline-block
      font-size: 20px
      line-height: 20px
      color: $dark-gray
      transition: color .3s
      padding-left: 40px
      margin-top: 20px
      position: relative
      &:before
        content: '\e806'
        font-family: Fontello
        font-style: normal
        font-weight: normal
        speak: never
        display: block
        margin: 0
        text-decoration: inherit
        text-align: center
        font-variant: normal
        text-transform: none
        font-size: 20px
        line-height: 20px
        position: absolute
        top: 0
        left: 1px
        color: $bronze-gold
      &:hover
        color: $bronze-gold
    time
      display: block
      padding-left: 40px
      margin-top: 16px
      font-size: 14px
      line-height: 20px
      font-style: normal
      position: relative
      &:before
        content: '\e809'
        font-family: Fontello
        font-style: normal
        font-weight: normal
        speak: never
        display: block
        margin: 0
        text-decoration: inherit
        text-align: center
        font-variant: normal
        text-transform: none
        font-size: 20px
        line-height: 20px
        position: absolute
        top: 0
        left: 0px
        color: $bronze-gold
    .emails
      display: flex
      flex-direction: column
      align-items: flex-start
      justify-content: flex-start
      padding-left: 40px
      margin-top: 20px
      font-size: 14px
      line-height: 20px
      font-style: normal
      position: relative
      &:before
        content: '\e804'
        font-family: Fontello
        font-style: normal
        font-weight: normal
        speak: never
        display: block
        margin: 0
        text-decoration: inherit
        text-align: center
        font-variant: normal
        text-transform: none
        font-size: 14px
        line-height: 20px
        position: absolute
        top: 0
        left: 0px
        color: $bronze-gold
      .email-link
        display: inline-block
        margin-top: 5px
        color: $dark-gray
        transition: color .3s
        font-size: 14px
        line-height: 20px
        &:nth-of-type(1)
          margin-top: 0
        &:hover
          color: $bronze-gold
    .get-offer
      margin-top: 30px
      @media (max-width: 414px)
        display: flex
        justify-content: center
        width: 100%
.drive-section
  background-repeat: no-repeat
  background-position: center
  background-size: cover
  position: relative
  height: calc(100vh - 80px)
  display: flex
  flex-direction: column
  align-items: flex-start
  justify-items: flex-start
  &:before
    content: ''
    display: block
    width: 100%
    height: 100%
    position: absolute
    top: 0
    left: 0
    background: linear-gradient(90deg, #181B1D 0%, rgba(24, 27, 29, 0) 100%)
  .container
    flex: 1 0 100%
    display: flex
    align-items: center
    justify-content: flex-start
    position: relative
    z-index: 1
  &__title
    @media (max-width: 414px)
      max-width: 235px
  &__content
    max-width: 540px
    margin-left: 8.5%
    color: #fff
    @media (max-width: 1400px)
      max-width: 445px
    @media (max-width: 960px)
      margin-left: 0
    @media (max-width: 768px)
      max-width: 600px
  &__descr
    margin-top: 60px
    font-size: 18px
    line-height: 27px
    @media (max-width: 414px)
      font-size: 16px
      line-height: 24px
      margin-top: 30px
  .test-link
    margin-top: 58px
    @media (max-width: 414px)
      display: flex
      justify-content: center
      width: 100%
      margin-top: 30px
  .scroll-btn
    position: absolute
    bottom: 0
    left: 0
    right: 0
    margin: 0 auto
    z-index: 1


/* video */
.video_wrapper
  position: absolute
  top: 0
  left: 0
  height: 100%
  width: 100%
  overflow: hidden
  .video-item
    min-height: 100%
    min-width: 100%
    position: absolute
    top: 50%
    left: 50%
    margin-right: -50%
    transform: translate(-50%, -50%)

/* fullpage lx / vx / txl page */
.fullpage-slider
  position: fixed
  top: 80px
  left: 0
  width: 100%
  overflow: hidden
  height: calc(100vh - 80px)
  @media (max-width: 1199px)
    position: relative
    top: 0
    margin-top: 80px
    overflow: auto
    height: auto
  @media (max-width: 960px)
    margin-top: 62px
  &__menu
    position: fixed
    top: 180px
    left: 5.5vw
    transition: transform .8s
    z-index: 10
    &.show
      transform: none !important
    .slider-menu__list
      margin: 0
      padding: 0
      list-style-type: none
      display: flex
      flex-direction: column
      align-items: flex-start
      justify-content: flex-start
      position: relative
      &:before
        content: ''
        display: block
        width: 1px
        height: calc(100% - 16px)
        background-color: rgba(#C4C4C4, .3)
        position: absolute
        top: 8px
        left: 6px
      .animation-line
        display: block
        position: absolute
        top: 0px
        left: 5px
        background-color: #c78e66
        width: 3px
        height: 50px
        border-radius: 5px
        transition: all .5s ease 0s
    .slider-menu__item
      display: inline-block
      padding-left: 10px
      margin-top: 0px
      &:nth-of-type(1)
        margin-top: 0
      &:nth-child(1).active~.animation-line
        top: 0
      &:nth-child(2).active~.animation-line
        top: 50px
      &:nth-child(3).active~.animation-line
        top: 100px
      &:nth-child(4).active~.animation-line
        top: 150px
      &:nth-child(5).active~.animation-line
        top: 200px
      &:nth-child(6).active~.animation-line
        top: 250px
      &:nth-child(7).active~.animation-line
        top: 300px
      &:nth-child(8).active~.animation-line
        top: 350px
      &:nth-child(9).active~.animation-line
        top: 400px
      &:nth-child(10).active~.animation-line
        top: 400px
      &.active
        .slider-menu__link
          font-size: 20px
          line-height: 20px
          color: #fff
    .slider-menu__link
      display: inline-block
      padding: 15px 38px
      text-transform: uppercase
      font-size: 14px
      line-height: 20px
      letter-spacing: 1px
      color: rgba(#fff, .75)
      transition: all .3s
      &:hover
        color: $bronze-gold
  &__wrapper
    display: flex
    flex-direction: column
    width: 100%
    height: 100%
    @media (max-width: 1199px)
      display: block
    & > section
      display: flex
      flex-direction: column
      align-items: flex-start
      justify-items: flex-start
      flex: 1 0 100%
      position: relative
      @media (max-width: 1199px)
        display: block
      .container
        flex: 1 0 100%
        position: relative
        z-index: 1
        @media (max-width: 1199px)
          min-height: calc(100vh - 80px)
        @media (max-width: 960px)
          min-height: calc(100vh - 62px)
          min-height: calc((var(--vh, 1vh) * 100) - 62px)
  &__scroll
    position: absolute
    top: 0
    right: 0
    width: 6px
    height: 100%
    background-color: $light-gray
    @media (max-width: 1199px)
      display: none
  &__drag-scroll
    position: absolute
    top: 0
    right: 0
    width: 100%
    background-color: $dark-gray
    cursor: pointer
    &:hover
      background-color: $bronze-gold
  .model-banner
    background-repeat: no-repeat
    background-position: center
    background-size: cover
    position: relative
    color: #fff
    &__title
      font-weight: 300
      font-size: 70px
      line-height: 70px
      @media (max-width: 420px)
        font-size: 50px
        line-height: 50px
    &__subtitle
      font-weight: 400
      font-size: 24px
      line-height: 29px
      display: inline-block
      margin-top: 18px
      margin-bottom: -20px
    &__price
      display: inline-block
      font-size: 35px
      line-height: 35px
      letter-spacing: 1px
      color: $bronze-gold
      text-transform: uppercase
      margin-top: 40px
      @media (max-width: 420px)
        margin-top: 20px
        font-size: 30px
        line-height: 30px
    .container
      display: flex
      align-items: flex-start
      justify-content: center
      flex-direction: column
      padding-left: 7.25%
      padding-top: 16px
    .model-btn
      margin-top: 40px
    .scroll-btn
      position: absolute
      bottom: 0
      left: 0
      right: 0
      margin: 0 auto
      z-index: 1

  .video-section
    background-repeat: no-repeat
    background-position: 50% 85%
    background-size: cover
    position: relative
    color: #fff
    &:after
      content: ''
      display: block
      width: 100%
      height: 100%
      position: absolute
      top: 0
      left: 0
      background: linear-gradient(0deg,#181b1d 0,rgba(24,27,29,0) 100%)
    .container
      display: flex
      flex-direction: column
      align-items: flex-start
      justify-content: space-between
    &__info
      max-width: 540px
      padding-top: 3.75%
      @media (max-width: 768px)
        padding-top: 40px
      .lx-image
        display: block
        width: auto
        max-width: 100%
        margin: 0
