        @media (max-width: 1024px)
          font-size: 16px
        @media (max-width: 768px)
          font-size: 14px
          line-height: 24px
        &:hover
          text-decoration: none
          color: $bronze-gold
        &:before
          content: ''
          display: block
          width: 32px
          height: 32px
          position: absolute
          top: 0
          left: 0
          background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='17' height='20' viewBox='0 0 17 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M10.413 0H1.71739C1.375 0 1.08333 0.123698 0.842391 0.371094C0.61413 0.605469 0.5 0.898438 0.5 1.25V18.75C0.5 19.1016 0.61413 19.3945 0.842391 19.6289C1.08333 19.8763 1.375 20 1.71739 20H15.2826C15.625 20 15.9103 19.8763 16.1386 19.6289C16.3795 19.3945 16.5 19.1016 16.5 18.75V6.25L10.413 0ZM15.2826 6.77734V6.875H9.80435V1.25H9.91848L15.2826 6.77734ZM1.71739 18.75V1.25H8.58696V8.125H15.2826V18.75H1.71739Z' fill='%23C78E66'/%3e%3c/svg%3e")
          background-repeat: no-repeat
          background-position: center
          background-size: 18px


/* error page */
.error-page
  height: auto
  margin-top: 80px
  position: relative
  @media (max-width: 960px)
    margin-top: 62px
  .container
    padding: 30px 0
    text-align: center
  &__title
    text-align: center
    font-size: 30px
    line-height: 30px
    letter-spacing: 2px
    text-transform: uppercase
    font-weight: 400
    color: $dark-black
    @media (max-width: 768px)
      font-size: 24px
      line-height: 25px
    @media (max-width: 414px)
      font-size: 22px
      line-height: 20px
    @media (max-width: 360px)
      font-size: 18px
      line-height: 20px
  & + .footer
    margin-top: auto


@import "_components/footer"
@import "_components/modals"
@import "_animates/_animates"