@import "settings"
.scroll-btn
  display: inline-flex
  align-items: center
  justify-content: center
  width: 60px
  height: 60px
  background-color: $dark-gray
  color: $bronze-gold
  transition: background-color .3s, color .3s
  .icon
    transform: rotate(90deg)
    font-size: 18px
    line-height: 20px
    &:before
      transition: transform .3s
  &:hover
    background-color: $bronze-gold
    color: #fff
    .icon
      &:before
        transform: translateX(3px)
.section-title
  font-weight: 300
  font-size: 60px
  line-height: 60px
  text-transform: uppercase
  @media (max-width: 1400px)
    font-size: 50px
    line-height: 55px
  @media (max-width: 768px)
    font-size: 40px
    line-height: 45px
  @media (max-width: 414px)
    font-size: 32px
    line-height: 38px
  @media (max-width: 360px)
    font-size: 27px
    line-height: 35px
.btn-style
  display: inline-flex
  align-items: center
  justify-content: flex-start
  position: relative
  text-transform: uppercase
  font-weight: 600
  font-size: 11px
  line-height: 18px
  letter-spacing: 2px
  padding: 14px 22px 15px 30px
  border: 1px solid
  transition: background-color .3s, color .3s, border-color .3s
  &:after
    content: '\e800'
    font-family: Fontello
    font-style: normal
    font-weight: normal
    speak: never
    display: block
    text-decoration: inherit
    text-align: center
    font-variant: normal
    text-transform: none
    font-size: 18px
    line-height: 20px
    margin: 0 0 0 13px
    color: $bronze-gold
    transition: transform .3s, color .3s
  &:hover
    &:after
      transform: translateX(5px)
  &.btn-fill-style
    &.black-style
      background-color: $dark-black
      border-color: $dark-gray
      color: #fff
      &:hover
        border-color: $bronze-gold
        background-color: $bronze-gold
        &:after
          color: #fff
    &.white-style
      background-color: #fff
      border-color: #fff
      color: $dark-gray
      &:hover
        border-color: $bronze-gold
        background-color: $bronze-gold
        color: #fff
        &:after
          color: #fff
  &.btn-stroke-style
    &.black-style
      color: $dark-gray
      border-color: $dark-gray
      &:hover
        border-color: $bronze-gold
        background-color: $bronze-gold
        color: #fff
        &:after
          color: #fff
    &.white-style
      color: #fff
      border-color: #fff
      &:hover
        border-color: $bronze-gold
        background-color: $bronze-gold
        color: #fff
        &:after
          color: #fff


