.modal
  display: none
  &.is-open
    display: block
  &[aria-hidden="false"]
    .modal__overlay
      animation: mmfadeIn 0.3s cubic-bezier(0, 0, 0.2, 1)
    .modal__container
      animation: mmslideIn 0.3s cubic-bezier(0, 0, 0.2, 1)
  &[aria-hidden="true"]
    .modal__overlay
      animation: mmfadeOut 0.3s cubic-bezier(0, 0, 0.2, 1)
    .modal__container
      animation: mmslideOut 0.3s cubic-bezier(0, 0, 0.2, 1)
  .modal__container, .modal__overlay
    will-change: transform

.modal__overlay
  position: fixed
  top: 0
  left: 0
  right: 0
  bottom: 0
  background: rgba($dark-black, .95)
  display: flex
  justify-content: center
  align-items: center
  z-index: 100

/* slider modal */
.modal__container
  background-color: $dark-black
  width: 100vw
  min-height: 100vh
  display: flex
  align-items: center
  justify-content: flex-start
  flex-direction: column
  padding: 50px 6.25vw 0
  overflow-y: scroll
  @include scrollbars(0, $scrl-bar_track, $scrl-bar_thumb, $scrl-bar_thumbh, 0)
  @media (max-width: 420px)
    padding: 40px 15px 0
.modal__header
  display: block
  text-align: left
  width: 100%
  .modal__title
    color: $gray-medium
    font-weight: 400
    font-size: 20px
    line-height: 20px
    letter-spacing: 1px
    text-transform: uppercase
  .modal__close
    background: transparent
    border: 0
    position: absolute
    top: 20px
    right: 20px
    color: $bronze-gold
    font-size: 40px
    line-height: 40px
    @media (max-width: 1024px)
      font-size: 30px
      line-height: 30px
    @media (max-width: 640px)
      font-size: 25px
      line-height: 25px
    @media (max-width: 360px)
      font-size: 20px
      line-height: 20px
      top: 10px
      right: 10px
.modal__content
  width: 100%
  margin-top: 20px
  flex: 1 1 100%
  height: 100%
  .modal-slider
    margin-left: auto
    margin-right: auto
    position: relative
    overflow: hidden
    list-style: none
    padding: 0 0 110px
    z-index: 1
    height: 100%
    .swiper-wrapper
      position: relative
      width: 100%
      height: 100%
      z-index: 1
      display: flex
      transition-property: transform,-webkit-transform
      box-sizing: content-box
    .swiper-slide
      flex-shrink: 0
      width: 100%
      height: 100%
      position: relative
      transition-property: transform,-webkit-transform
    .slide-item
      margin: 0 7% 0 0
      max-width: 46.5%
      @media (max-width: 640px)
        max-width: 100%
        margin: 0
      &:last-child
        margin-right: 0
      .image-block
        .modal-image
          display: block
          width: auto
          max-width: 100%
      .descr-block
        margin-top: 30px
        @media (max-width: 1400px)
          margin-top: 15px
        .slide-title
          font-weight: 300
          font-size: 40px
          line-height: 40px
          color: #fff
          text-transform: uppercase
          position: relative
          @media (max-width: 1400px)
            font-size: 32px
            line-height: 40px
          @media (max-width: 960px)
            font-size: 28px
            line-height: 35px
          @media (max-width: 768px)
            font-size: 24px
            line-height: 30px
          @media (max-width: 360px)
            font-size: 22px
          &:after
            content: ''
            display: block
            width: 105px
            height: 1px
            background-color: $bronze-gold
            position: absolute
            bottom: -30px
            left: 0
            @media (max-width: 1400px)
              bottom: -20px
        .slide-descr
          position: relative
          margin-top: 60px
          font-size: 18px
          line-height: 27px
          color: #fff
          overflow-y: scroll
          @include scrollbars(0, $scrl-bar_track, $scrl-bar_thumb, $scrl-bar_thumbh, 0)
          @media (max-width: 1400px)
            margin-top: 40px
          @media (max-width: 1200px)
            font-size: 16px
            line-height: 25px
          @media (max-width: 768px)
            font-size: 14px
            line-height: 22px
    .swiper-pagination
      display: block
      color: #fff
      text-transform: uppercase
      font-size: 18px
      line-height: 18px
      letter-spacing: 1px
      position: absolute
      left: 0
      right: 0
      margin: 0 auto
      bottom: 40px
      width: auto
      text-align: center
    .swiper-navigation
      position: absolute
      bottom: 18px
      left: 0
      display: flex
      align-items: center
      justify-content: center
      width: 100%
      .nav-btn
        width: 60px
        height: 60px
        flex: 0 0 60px
        display: flex
        align-items: center
        justify-content: center
        background-color: $bronze-gold
        color: #fff
        border-radius: 50%
        margin: 0 60px
        margin: 0 60px
        font-size: 24px
        line-height: 24px
        cursor: pointer
        transition: background-color .3s, color .3s
        &.btn-prev
          .icon
            transform: scale(-1,1)
        &.swiper-button-disabled
          background-color: $dark-gray
          color: $gray-medium

/* simple modal */
.simple-modal
  .modal__container
    max-width: 540px
    height: auto
    padding: 30px
    color: #fff
  .modal__close
    font-size: 20px
    line-height: 20px
    top: 10px
    right: 10px
  .modal__title
    color: #fff
    font-size: 24px
    line-height: 30px
  .modal__subtitle
    font-weight: 300
    font-size: 18px
    line-height: 24px
    margin-top: 20px
  .modal__content
    form
      margin-top: 30px
      .input-group
        width: 100%
        margin-top: 30px
        &:nth-of-type(1)
          margin-top: 0
        input
          width: 100%
          background-color: transparent
          border: 1px solid $gray-medium
          color: #fff
          font-size: 18px
          line-height: 20px
          padding: 15px 20px
          @media (max-width: 420px)
            font-size: 16px
            padding: 10px 15px
        button
          display: flex
          justify-content: center
          width: 100%
          font-size: 18px
          line-height: 18px
          padding: 15px
          @media (max-width: 420px)
            font-size: 16px



@keyframes mmfadeIn
  from
    opacity: 0

  to
    opacity: 1

@keyframes mmfadeOut
  from
    opacity: 1

  to
    opacity: 0

@keyframes mmslideIn
  from
    transform: translateY(-15%)

  to
    transform: translateY(0)

@keyframes mmslideOut
  from
    transform: translateY(0)

  to
    transform: translateY(10%)