.footer
  background-color: $dark-gray
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.1)
  padding: 40px 0
  @media (max-width: 960px)
    padding: 20px 0
  &__top
    display: flex
    align-items: center
    justify-content: space-between
    flex-wrap: wrap
    @media (max-width: 414px)
      justify-content: center
    .logo-link
      display: inline-flex
      align-items: center
      justify-content: flex-start
      position: relative
      @media (max-width: 340px)
        margin: 0 30px 20px
      &:after
        content: ''
        display: inline-block
        width: 1px
        height: 26px
        background-color: $bronze-gold
        margin: 5px 0 5px 28px
        @media (max-width: 414px)
          margin: 5px 0 5px 20px
        @media (max-width: 340px)
          display: none
      .logo-image
        display: inline-block
        width: auto
        max-width: 100%
    .add-link
      display: inline-block
      color: #fff
      font-size: 12px
      line-height: 12px
      letter-spacing: 1px
      margin-left: 1.55%
      text-transform: uppercase
      transition: color .3s
      @media (max-width: 414px)
        margin: 0 0 0 20px
      @media (max-width: 340px)
        margin: 0
      &:hover
        color: $bronze-gold
    .footer-menu
      margin-left: auto
      @media (max-width: 414px)
        margin: 20px auto 0
      &__list
        display: flex
        align-items: center
        justify-content: flex-start
      &__item
        flex: 0 0 auto
        width: auto
        margin-left: 40px
        &:nth-of-type(1)
          margin-left: 0
      &__link
        display: inline-block
        color: #fff
        font-weight: 600
        font-size: 12px
        line-height: 12px
        letter-spacing: 1px
        text-transform: uppercase
        transition: color .3s
        padding: 8px 0
        &:hover
          color: $bronze-gold
  &__bottom
    display: flex
    align-items: flex-start
    justify-content: flex-start
    flex-wrap: wrap
    margin-top: 42px
    @media (max-width: 640px)
      justify-items: center
    @media (max-width: 414px)
      text-align: center
      margin-top: 25px
    .phone-link
      display: flex
      flex-direction: column
      align-items: flex-start
      justify-content: flex-start
      color: #fff
      transition: color .3s
      font-size: 20px
      line-height: 20px
      @media (max-width: 414px)
        justify-content: center
        align-items: center
        margin: 0 auto
      &:hover
        color: $bronze-gold
      span
        display: inline-block
        margin-bottom: 10px
        color: $gray-light
        font-size: 12px
        line-height: 18px
        font-weight: 500
    address
      display: flex
      flex-direction: column
      align-items: flex-start
      justify-content: flex-start
      font-size: 12px
      line-height: 20px
      font-style: normal
      color: #fff
      margin-left: 6.2%
      @media (max-width: 640px)
        margin-left: auto
      @media (max-width: 414px)
        flex: 0 0 100%
        max-width: 100%
        width: 100%
        justify-content: center
        align-items: center
        margin: 24px auto 0
      span
        display: inline-block
        margin-bottom: 10px
        color: $gray-light
        font-size: 12px
        line-height: 18px
        font-weight: 500
    .social-list
      flex: 0 0 auto
      width: auto
      margin: 0 0 0 auto
      display: flex
      align-items: center
      justify-content: flex-end
      flex-wrap: wrap
      @media (max-width: 640px)
        margin: 20px auto 0
        justify-items: center
      &__item
        flex: 0 0 auto
        width: auto
        margin-left: 20px
        @media (max-width: 768px)
          margin-left: 15px
        @media (max-width: 640px)
          margin: 0 5px
      &__link
        display: inline-flex
        align-items: center
        justify-content: center
        width: 50px
        height: 50px
        color: #fff
        background-color: $dark-black
        border-radius: 50%
        font-size: 25px
        line-height: 24px
        transition: color .3s
        @media (max-width: 768px)
          width: 40px
          height: 40px
          font-size: 20px
          line-height: 20px
        &:hover
          color: $bronze-gold

  &__copyright
    margin-top: 52px
    padding: 40px 0
    border-top: 1px solid rgba(#fff, .2)
    color: $gray-light
    font-size: 12px
    line-height: 18px
    font-weight: 500
    @media (max-width: 960px)
      padding: 20px 0 0
    @media (max-width: 640px)
      margin-top: 20px
      text-align: center
    p
      display: inline-block
    .privacy-link
      display: inline-block
      color: #fff
      margin-left: 1.5%
      transition: color .3s
      @media (max-width: 768px)
        margin-left: 0
        margin-top: 10px
      &:hover
        color: $bronze-gold